import { CanvasCore } from '../CanvasCore';

export class Core {
    canvasCore: CanvasCore;
    pos: number[][] = [];
    col: number[][] = [];
    constructor(canvasCore: CanvasCore) {
        this.canvasCore = canvasCore;
        for (let i = 0; i < 300; ++i) {
            const origin = Math.random() * this.canvasCore.canvas.height;
            this.pos.push([Math.random() * this.canvasCore.canvas.width, origin, origin, Math.random()]);
            this.col.push([100 + Math.random() * 250, 0, 0, Math.random()]);
        }
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#eeeeee';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.fill();

        for (let i = 0; i < this.pos.length; ++i) {
            this.pos[i][1] += this.pos[i][3] * 7;
            if (this.pos[i][1] > ctx.canvas.height) { this.pos[i][1] = 0; }
            ctx.fillStyle = `rgba(${this.col[i][0]},${this.col[i][1]},${this.col[i][2]}, ${this.col[i][3]})`;
            ctx.beginPath();
            ctx.arc(this.pos[i][0], this.pos[i][1], 10, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
    }
    MouseDown(e: MouseEvent) {
        console.log(e, 'core');
    }
    MouseUp(e: MouseEvent) {
        console.log(e, 'core');
    }
    MouseMove(e: MouseEvent) {
        console.log(e, 'core');
    }
    MouseDrag(e: MouseEvent) {
        console.log(e, 'core');
    }
}